import Vue from 'vue'
import Router from 'vue-router'
import storage from 'good-storage'
import home from '../views/home.vue'
import login from '../views/login.vue'
import monitoring from '../views/monitoring.vue'
import bedManage from '../views/bedManage.vue'
import deviceManage from '../views/deviceManage.vue'
import accountManage from '../views/accountManage.vue'
import history from '../views/history.vue'


Vue.use(Router)

const router =  new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'login',
      component:login ,
    },
    {
      path: '/home',
      name: 'home',
      component: home,
    },
    {
      path: '/monitoring',
      name: 'monitoring',
      component: monitoring,
    },
    {
      path: '/bedManage',
      name: 'bedManage',
      component: bedManage,
    },
    {
      path: '/deviceManage',
      name: 'deviceManage',
      component: deviceManage,
    },
    {
      path: '/accountManage',
      name: 'accountManage',
      component: accountManage,
    },
    {
      path: '/history',
      name: 'history',
      component: history,
    },
  ]
})


/* 全局路由守卫 */
router.beforeEach((to, from, next) => {
  /* to: Route: 即将要进入的目标 路由对象
  from: Route: 当前导航正要离开的路由
  next: Function: 一定要调用该方法来 resolve 这个钩子。执行效果依赖 next 方法的调用参数。*/

  const nextRoute = ['accountManage', 'bedManage', 'deviceManage','home','history','monitoring'];
  const nextRoute1 = ['accountManage', 'bedManage', 'deviceManage'];
  let canGo;
  /*判断账号是否有权限*/
  console.log(storage.get("userInfo"))
  console.log(to.path)
  console.log(Boolean(storage.get("userInfo").token))
  if(Boolean(storage.get("userInfo").token)){/*已登录*/
    if(to.path === '/login'){/*登陆后跳转登录页-->结果跳转home*/
      next({path:'/home'})
    }else if(to.path === '/'){/*登陆后跳转登录页-->结果跳转home*/
      next({path:'/home'})
    }else{
      if(storage.get("userInfo").role==2){/*登陆后账号角色为科室管理员*/
        if (nextRoute1.indexOf(to.name) >= 0) {/*登陆后账号角色为科室管理员,如果要跳转这三个页面-->结果跳转home*/
            next({path:'/home'})
        }else{
          next()
        }
      }else{
        next()
      }
    }
  }else{/*未登录*/
    if (nextRoute.indexOf(to.name) >= 0){/*未登录且跳转到这六个页面-->结果跳转login*/
      next({path:'/'})
    }else{
      next()
    }
  }
// next()
});
export default router;



